.features 
    display: grid
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr))
    gap: 30px
    

    &__item 
        padding: 38px 20px 20px 34px
        min-height: 150px
        font-weight: 300
        font-size: 20px
        line-height: 34px
        background: #FFFFFF
        box-shadow: 0px 0px 15px rgba(143, 149, 172, 0.15)
        border-radius: 5px
        transition: box-shadow 0.3s ease

        &:hover 
            box-shadow: 0px 0px 20px rgba(143, 149, 172, 0.4)
    
    &__item-title
        margin-bottom: 4px
        font-weight: 700
        font-size: 35px
        line-height: 40px